import { TimePeriod, PaymentMode, Frequency } from './constants';

const helpers = (context) => {
  const i18nFilter = context.$filter('i18n');

  return {
    /**
     * @description
     * Returns either label like now, today or yesterday or the actual date
     *
     * @name dateLabel
     * @type {function}
     * @param {string} date date
     *
     * @returns {string}
     */
    dateLabel: (date) => i18nFilter(TimePeriod[date]) || date,
    /**
     * @description
     * Gets translated string of payment mode
     *
     * @name getPaymentModeLabel
     * @type {function}
     * @param {string} mode payment mode
     *
     * @returns {string}
     */
    getPaymentModeLabel: (mode) => i18nFilter(PaymentMode[mode] || PaymentMode.SINGLE),
    /**
     * @description
     * Gets translated strings of payment frequency
     *
     * @name getFrequencyLabel
     * @type {function}
     * @param {string} frequency frequency
     *
     * @returns {string}
     */
    getFrequencyLabel: (frequency) => i18nFilter(Frequency[frequency]),
    /**
     * @description
     * Gets strings of exchange rate
     *
     * @name getRateLabel
     * @type {function}
     * @param {object} rate rate state
     *
     * @returns {string}
     */
    getRateLabel: (rate) =>
      `1 ${rate.requestParams.currencyFrom} = ${rate.value} ${rate.requestParams.currencyTo}`,
    /**
     * @description
     * Determines whether or not exchange rate should be shown
     * if account currency and payment currency the same
     *
     * @name showExchangeRate
     * @type {function}
     * @param {object} instructedAmount Information about payment instructed amount
     * @param {object} rate rate state
     *
     * @returns {string}
     */
    showExchangeRate: (instructedAmount, rate) =>
      rate.requestParams.currencyFrom !== instructedAmount.currencyCode,
  };
};

export default helpers;



